.products{
    padding-top: 190px;
    padding-bottom: 80px;
    position: relative;
    background: white;

    &>img{
        position: absolute;
        top: 3%;
        left: -14%;
        width: 35%;
        height: auto;
        z-index: 0;
    }
    &Wrapper{
        position: relative;
        z-index: 1;
        max-width: 1510px;
        margin: auto;
        text-align: center;
        & h2{
            color: #2dcbe5;
        }
        & span{
            font-size: 2.5rem;
            font-weight: 700;
        }
        & p{
            font-size: 2.5rem;
            font-weight: 200;
        }
    }
    &Metagunner{
        margin-top: 50px;
        & img{
            box-shadow: 0 0 20px black;
            transition: .3s ease-in-out;
        }
    }
    &Metagunner:hover>img{
        opacity: 0.5;
        box-shadow: none;
    }
    &Metagunner:hover &BTN{
        opacity: 1;
    }
    &BTN{
        opacity: 0;
        position: absolute;
        top: 85%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 2rem;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: center;
        transition: .3s ease-in-out;
        & a img{
        border-radius: 10px;
        }
        & a{
            display: flex;
            align-items: center;
            color: black;
        }
        & a:first-child {
            padding: 5px 20px;
            border-radius: 25px;
            color: white;
            font-weight: 400;
            background: linear-gradient(90deg, rgba(98,230,108,1) 0%, rgba(87,134,197,1) 35%, rgba(45,203,229,1) 100%);
        }
        &web{
            box-shadow: none !important;
            width: 60px;
            height: 60px;
            filter: brightness(0) invert(1);
        }
        &CHPlay{
            margin-left: 20px;
            width: 250px;
            height: 75px;
        }
    }
}
@media screen and (max-width: 768px) {
    .productsBTN{
        font-size: 1rem;
        &web{
            box-shadow: none !important;
            width: 30px;
            height: 30px;
            filter: brightness(0) invert(1);
        }
        &CHPlay{
            width: 200px;
            height: 60px;
        }
    }
}
@media screen and (max-width: 768px) {
    .productsBTN{
        opacity: 1;
        top: 100%;
    }
}
@media screen and (max-width: 500px) {
    .productsBTN{
        &web{
            width: 30px;
            height: 30px;
        }
        &CHPlay{
            width: 200px;
            height: 60px;
        }
    }
}
@media screen and (max-width: 500px) {
    .productsBTN{
        & a:first-child {
            padding: 13px 20px;
            margin-bottom: 10px;
            margin-left: 15px;
        }
    }
}