.Aboutc{
    padding-top: 100px;
    text-align: center;
    position: relative;
    color: #144150;
    background: white;
    overflow: hidden;
    &>img{
        position: absolute;
        top: -80%;
        left: 90%;
        width: 844px;
        height: auto;
        z-index: 0;
    }
    &_wrapper{
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }
    &_item{
        min-width: 300px;
        width: 25%;
        height: 100%;
    }
    & button{
        background: orange;
        border: 0;
        padding: 10px;
        border-radius: 20px;
    }
}