.banner{
    position: relative;
    height: 80vh;
    background: white;
    z-index: 1;
    &Background{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
    }
    &Image{
        position: absolute;
        height: 100%;
        width: 100%;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
            & img{
            position: absolute;
            left: unset;
            right:  0;
            width: unset;
            height: unset;
            top: 0;
            min-width: unset;
        }
    }
    &Content{
        position: relative;
        width: 50%;
        top: 10%;
        left: 0;
        padding: 100px 0;
        & h1{
            font-size: 4rem;
            font-weight: 200;
        }
        & p{
            color: #1e5262;
            margin-bottom: 40px;
            line-height: 2;
        }
        & a{
            padding: 20px 50px;
            border-radius: 25px;
            color: white;
            font-weight: 700;
            background: linear-gradient(90deg, rgba(98,230,108,1) 0%, rgba(87,134,197,1) 35%, rgba(45,203,229,1) 100%);
        }
    }
}
@media screen and (max-width: 1024px) {
    .bannerContent{
        width: 75%;
        top: 40%;
        left: 5%;
    }
   }
@media screen and (max-width: 768px) {
 .bannerContent{
     top: 43%;
     left: 0;
     width: 100%;
     text-align: center;
 }
}
@media screen and (max-width: 600px) {
    .bannerImage img{
        width: 100%;
    }
}
@media screen and (max-width: 500px) {
    .bannerContent{
        top: 25%;
        & h1{
            font-size: 3rem;
        }
    }
}
@media screen and (max-width: 400px) {
    .bannerContent{
        & h1{
            font-size: 2rem;
            font-weight: 400;
        }
    }
}